import { createServerSupabaseClientWithCookies } from '@/lib/supabase/server';
import { NextRequest, NextResponse } from 'next/server';
import { cookies } from 'next/headers';

/**
 * ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥ API ÏóîÎìúÌè¨Ïù∏Ìä∏
 * Ï∫îÎîî Í∞úÏàòÎ•º Ìè¨Ìï®Ìïú ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥Î•º Î∞òÌôòÌï©ÎãàÎã§.
 */
export async function GET(request: NextRequest) {
  try {
    console.log('üîç [User Profile API] ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ ÏöîÏ≤≠ Î∞õÏùå');

    // URLÏóêÏÑú userId ÌååÎùºÎØ∏ÌÑ∞ Ï∂îÏ∂ú
    const { searchParams } = new URL(request.url);
    const requestedUserId = searchParams.get('userId');

    // App RouterÏóêÏÑú Ïø†ÌÇ§Î•º ÏùΩÏùÑ Ïàò ÏûàÎäî ÏÑúÎ≤ÑÏÇ¨Ïù¥Îìú Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ ÏÉùÏÑ±
    const cookieStore = await cookies();
    
    const supabase = createServerSupabaseClientWithCookies({
      get: (name: string) => {
        const cookie = cookieStore.get(name);
        return cookie ? { name: cookie.name, value: cookie.value } : undefined;
      },
      set: (cookie: { name: string; value: string; [key: string]: any }) => {
        cookieStore.set(cookie.name, cookie.value, cookie);
      }
    });

    console.log('üîç [User Profile API] Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ ÏÉùÏÑ± ÏôÑÎ£å');

    // Ïù∏Ï¶ùÎêú ÏÇ¨Ïö©Ïûê ÌôïÏù∏
    const { data: userData, error: userError } = await supabase.auth.getUser();
    
    if (userError || !userData?.user) {
      console.warn('‚ö†Ô∏è [User Profile API] Ïù∏Ï¶ùÎêòÏßÄ ÏïäÏùÄ ÏöîÏ≤≠:', userError);
      return NextResponse.json(
        { 
          success: false,
          error: 'Unauthorized',
          message: 'Ïù∏Ï¶ùÏù¥ ÌïÑÏöîÌï©ÎãàÎã§.'
        }, 
        { status: 401 }
      );
    }

    const currentUserId = userData.user.id;
    
    // ÏöîÏ≤≠Îêú ÏÇ¨Ïö©Ïûê IDÍ∞Ä ÌòÑÏû¨ Ïù∏Ï¶ùÎêú ÏÇ¨Ïö©ÏûêÏôÄ Îã§Î•∏ Í≤ΩÏö∞ Í∂åÌïú Ï≤¥ÌÅ¨
    if (requestedUserId && requestedUserId !== currentUserId) {
      console.warn('‚ö†Ô∏è [User Profile API] Îã§Î•∏ ÏÇ¨Ïö©ÏûêÏùò ÌîÑÎ°úÌïÑ ÏöîÏ≤≠ ÏãúÎèÑ:', {
        currentUserId,
        requestedUserId
      });
      return NextResponse.json(
        { 
          success: false,
          error: 'Forbidden',
          message: 'Îã§Î•∏ ÏÇ¨Ïö©ÏûêÏùò ÌîÑÎ°úÌïÑÏóê Ï†ëÍ∑ºÌï† Ïàò ÏóÜÏäµÎãàÎã§.'
        }, 
        { status: 403 }
      );
    }

    // ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥ Ï°∞Ìöå (Ï∫îÎîî Ï†ïÎ≥¥ + Í¥ÄÎ¶¨Ïûê Í∂åÌïú Ìè¨Ìï®)
    const { data: profile, error: profileError } = await supabase
      .from('user_profiles')
      .select(`
        id,
        email,
        nickname,
        avatar_url,
        star_candy,
        star_candy_bonus,
        is_admin,
        is_super_admin,
        created_at,
        updated_at
      `)
      .eq('id', currentUserId)
      .single();

    if (profileError) {
      console.error('‚ùå [User Profile API] ÌîÑÎ°úÌïÑ Ï°∞Ìöå Ïò§Î•ò:', profileError);
      return NextResponse.json(
        { 
          success: false,
          error: 'Profile not found',
          message: 'ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑÏùÑ Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.'
        }, 
        { status: 404 }
      );
    }

    // Provider Ï†ïÎ≥¥ Ï∂îÏ∂ú (Supabase authÏóêÏÑú Í∞ÄÏ†∏Ïò§Í∏∞)
    const user = userData.user;
    let provider = 'email'; // Í∏∞Î≥∏Í∞í
    let providerDisplayName = 'Ïù¥Î©îÏùº';
    
    // user.identitiesÏóêÏÑú provider Ï†ïÎ≥¥ ÌôïÏù∏
    if (user.identities && user.identities.length > 0) {
      const identity = user.identities[0]; // Ï≤´ Î≤àÏß∏ identity ÏÇ¨Ïö©
      provider = identity.provider || 'email';
      
      // ProviderÎ≥Ñ ÌïúÍµ≠Ïñ¥ ÌëúÏãúÎ™Ö ÏÑ§Ï†ï
      switch (provider) {
        case 'google':
          providerDisplayName = 'Google';
          break;
        case 'kakao':
          providerDisplayName = 'Kakao';
          break;
        case 'apple':
          providerDisplayName = 'Apple';
          break;
        case 'github':
          providerDisplayName = 'GitHub';
          break;
        case 'facebook':
          providerDisplayName = 'Facebook';
          break;
        case 'twitter':
          providerDisplayName = 'Twitter';
          break;
        case 'discord':
          providerDisplayName = 'Discord';
          break;
        case 'email':
        default:
          providerDisplayName = 'Ïù¥Î©îÏùº';
          break;
      }
    }

    console.log('‚úÖ [User Profile API] ÌîÑÎ°úÌïÑ Ï°∞Ìöå ÏÑ±Í≥µ:', {
      userId: profile.id,
      nickname: profile.nickname,
      star_candy: profile.star_candy,
      star_candy_bonus: profile.star_candy_bonus,
      total: (profile.star_candy || 0) + (profile.star_candy_bonus || 0),
      is_admin: profile.is_admin,
      is_super_admin: profile.is_super_admin,
      provider: provider,
      providerDisplayName: providerDisplayName
    });

    return NextResponse.json({
      success: true,
      user: {
        id: profile.id,
        email: profile.email,
        name: profile.nickname,
        avatar_url: profile.avatar_url,
        star_candy: profile.star_candy || 0,
        star_candy_bonus: profile.star_candy_bonus || 0,
        total_candy: (profile.star_candy || 0) + (profile.star_candy_bonus || 0),
        is_admin: profile.is_admin || false,
        is_super_admin: profile.is_super_admin || false,
        provider: provider,
        provider_display_name: providerDisplayName,
        created_at: profile.created_at,
        updated_at: profile.updated_at
      }
    });

  } catch (error) {
    console.error('üí• [User Profile API] Ï≤òÎ¶¨ Ï§ë Ïò§Î•ò:', error);
    
    return NextResponse.json(
      { 
        success: false,
        error: 'Internal server error',
        message: 'ÏÑúÎ≤Ñ ÎÇ¥Î∂Ä Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.'
      }, 
      { status: 500 }
    );
  }
}

/**
 * OPTIONS ÏöîÏ≤≠ Ï≤òÎ¶¨ (CORS ÏßÄÏõê)
 */
export async function OPTIONS(request: NextRequest) {
  return new NextResponse(null, {
    status: 200,
    headers: {
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Methods': 'GET, OPTIONS',
      'Access-Control-Allow-Headers': 'Content-Type, Authorization',
    },
  });
} 